import React from 'react';
import './WindowHeader.css';

import { useHistory } from 'react-router-dom';

import leftImage from '../../images/window-header__button-image_left.svg';

function WindowHeader(props) {
  const history = useHistory();

  function handleButtonBack() {
    if(props.buttonBackCallback !== undefined && typeof props.buttonBackCallback === 'function') {
      props.buttonBackCallback();
    }
    history.goBack();
  }

  return (
    <header className="window-header">
      {props.standartLeftComponent
        ? (<button className="window-header__button" onClick={history.goBack}>
            <img className="window-header__button-image" src={leftImage} alt="" />
          </button>)
        : props.leftComponent
      }
      {props.middleComponent}
      {props.standartRigthComponent
        ? (<p className="window-header__button-text" onClick={handleButtonBack}>Готово</p>)
        : props.rigthComponent
      }
    </header>
  );
}

export default WindowHeader;

