import React from 'react';
import './Profile.css';

import ProfileCard from '../ProfileCard/ProfileCard';
import Navigation from '../Navigation/Navigation'

import { Link } from 'react-router-dom';

import settingImage from '../../images/profile__setting-image_setting.svg';
import mediafileImage from '../../images/profile__setting-image_mediafile.svg';
import accountImage from '../../images/profile__setting-image_account.svg';

function Profile(props) {
  const [overlayActiveIs, setOverlayActiveIs] = React.useState(false);

  function closeOverlay(e) {
    if(e.target.classList.value.indexOf('overlay') > -1 ) {
      setOverlayActiveIs(false);
    }
  }
  
  return (
  <>
    <section className="profile">
      <div className="profile__account">
        <div className="profile__account-user" onClick={() => {setOverlayActiveIs(true)}}>
          <img className="profile__account-user-avatar" src={props.yourProfile.image[0]} alt="" />
          <p className="profile__account-user-name">{props.yourProfile.name} {props.yourProfile.year}</p>
        </div>
        <div className="profile__settings">
          <Link className="profile__setting" to="/app-setting">
            <div className="profile__setting-button">
              <img className="profile__setting-button-image" src={settingImage} alt="Настройки" />
            </div>
            <p className="profile__setting-text">Настройки</p>
          </Link>
          <Link className="profile__setting profile__setting_middle" to="/mediafile-setting">
            <div className="profile__setting-button">
              <img className="profile__setting-button-image" src={mediafileImage} alt="Добавить медиафайлы" />
            </div>
            <p className="profile__setting-text">Добавить медиафайлы</p>
          </Link>
          <Link className="profile__setting" to="/account-setting">
            <div className="profile__setting-button">
              <img className="profile__setting-button-image" src={accountImage} alt="Изменить информация" />
            </div>
            <p className="profile__setting-text">Изменить информация</p>
          </Link>
        </div>
      </div>
      <div className="profile__additional">
  
      </div>
      <Navigation />
    </section>
    <div className={`overlay ${overlayActiveIs ? 'overlay_active' : ''}`} onClick={closeOverlay}>
      <div className="overlay__column">
        <ProfileCard card={props.yourProfile}/>
      </div>
    </div>
  </>
  );
}

export default Profile;