import React from 'react';
import './Register.css';

import Header from '../Header/Header'
import ButtonSubmit from '../ButtonSubmit/ButtonSubmit';

function Register(props) {
  const [name, setName] = React.useState('');
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');

  return (
    <section className="register">
      <Header />
      <div className="register__column">
        <form className="form" name="registration">
          <label className="form__label">
            <p className="form__text">Имя</p>
            <input
              className={`form__input ${false ? 'form__input_disabled' : ''}`}
              name="registration"
              id="name-input"
              type="text"
              value={name}
              minLength="2"
              maxLength="30"
              required
            />
            <span className="form__error" id="name-input-error"></span>
          </label>
          <label className="form__label">
            <p className="form__text">E-mail</p>
            <input
              className={`form__input ${false ? 'form__input_disabled' : ''}`}
              name="registration"
              id="email-input"
              type="email"
              value={email}
              required
            />
            <span className="form__error" id="email-input-error"></span>
          </label>
          <label className="form__label">
            <p className="form__text">Пароль</p>
            <input className={`form__input ${false ? 'form__input_disabled' : ''}`}
              name="registration"
              id="password-input"
              type="password"
              value={password}
              minLength="8"
              required
            />
            <span className="form__error" id="password-input-error"></span>
          </label>
          <div></div>
          <ButtonSubmit
            handleSubmit={() => {}}
            buttonText="Зарегистрироваться"
            text="Уже зарегистрированы?"
            link__link="./signin"
            link__text="Войти"
          />
        </form>
      </div>
    </section>
  );
}

export default Register;