import React from 'react';
import './Login.css';

import Header from '../Header/Header'
import ButtonSubmit from '../ButtonSubmit/ButtonSubmit';

function Login(props) {
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');

  return (
    <section className="login">
      <Header />
      <div className="login__column">
      <form className="form" name="login">
          <label className="form__label">
            <p className="form__text">E-mail</p>
            <input
              className={`form__input ${true ? 'form__input_disabled' : ''}`}
              name="login"
              id="email-input"
              type="email"
              value={email}
              required
            />
            <span className="form__error" id="email-input-error">{}</span>
          </label>
          <label className="form__label">
            <p className="form__text">Пароль</p>
            <input
              className={`form__input ${false ? 'form__input_disabled' : ''}`}
              name="login"
              id="password-input"
              type="password"
              minLength="8"
              value={password}
              required
            />
            <span className="form__error" id="password-input-error">{}</span>
          </label>
          <div></div>
          <ButtonSubmit
            handleSubmit={() => {}}
            buttonText="Войти"
            text="Ещё не зарегистрированы?"
            link__link="./signup"
            link__text="Регистрация"
          />
        </form>
      </div>
    </section>
  );
}

export default Login;