import React from 'react';
import './Chat.css';

import WindowHeader from '../WindowHeader/WindowHeader';
import ProfileCard from '../ProfileCard/ProfileCard';

function Chat(props) {
  const [overlayActiveIs, setOverlayActiveIs] = React.useState(false);
  function closeOverlay(e) {
    if(e.target.classList.value.indexOf('overlay') > -1 ) {
      setOverlayActiveIs(false);
    }
  }

  const messages = React.useRef();

  //const [chat, setChat] = React.useState([]);
  function scrollMessagesDown() {
    messages.current.scrollTop = messages.current.offsetHeight;
  }

  React.useEffect(() => {
    //setChat(props.chat)
    console.log(11)
    scrollMessagesDown();
  }, []);

  const chatEnter = React.useRef();
  const [text, setText] = React.useState("");

  function handleTextarea() {
    chatEnter.current.style.height = 'auto'
    chatEnter.current.style.height = chatEnter.current.scrollHeight - 4 + "px"
    setText(chatEnter.current.value);
  }

  function changeDisaibledButtonSend() {
    return text === ''
  }

  function handleSendButton() {
    //props.chat
    props.handleSendButton(text)
      .then(() => {
        scrollMessagesDown();
        setText("")
      })

  }

  const middleComponent = (
    <div className="messages__chat messages__chat_size_small" onClick={() => {setOverlayActiveIs(true)}}>
      <img className="messages__chat-image messages__chat-image_size_small" src={props.otherProfile.image[0]} alt="" />
      <p className="messages__chat-name messages__chat-name_size_small">{props.otherProfile.name}</p>
    </div>
  );

  return (
  <>
    <section className="chat">
      <WindowHeader
        standartLeftComponent={true}
        middleComponent={middleComponent}
        standartRigthComponent={false}
      />
      <div className="chat__messages" ref={messages}>
        {
          props.chat
          ? props.chat.map((message, i) => {
              if(message.otherIs !== undefined) {
                return (
                  <div className="chat__message">
                    <div className={`chat__message-block ${message.otherIs ? 'chat__message-block_otherIs_true' : 'chat__message-block_otherIs_false'}`} key={i}>
                      <p className="chat__message-text">{message.text}</p>
                    </div>
                  </div>
                )
              } else {
                switch (message.type) {
                  case 'date':
                    return (
                      <p className="chat__message-date" key={i}>{message.text}</p>
                    )
                    default:
                      return <></>;
                }
              }
            })
          : <></>
          }
      </div>
      <div className="chat__input">
        <div className="chat__enter-block">
          <textarea className="chat__enter" ref={chatEnter} onChange={handleTextarea} type="text" value={text} placeholder="Введите сообщение"/>
        </div>
        <button className="chat__send" onClick={handleSendButton} disabled={changeDisaibledButtonSend()}>ОТПРАВИТЬ</button>
      </div>
    </section>
    <div className={`overlay ${overlayActiveIs ? 'overlay_active' : ''}`} onClick={closeOverlay}>
      <div className="overlay__column">
        <ProfileCard card={props.otherProfile}/>
      </div>
    </div>
  </>
  );
}

export default Chat;

